/* eslint no-underscore-dangle: ["error", { "allow": ["_push", "_url"] }] */

import getBaseUrl from './base-url';
import * as config from '../config';

// const navigator = weex.requireModule('navigator');
const navigator = weex.requireModule('myNavigator') || weex.requireModule('navigator');
// const modal = weex.requireModule('modal');
const navEvent = weex.requireModule('myNavigatorEvent');

/**
 * object  URL 
 */
function createQuery(obj) {
  if (obj === undefined || obj === null) {
    return '';
  }

  let url = '?';
  // for (const key in obj) {
  //   if (obj[key] !== null) {
  //     url += (`${key}=${encodeURIComponent(obj[key])}&`);
  //   }
  // }
  Object.keys(obj).forEach((key) => {
    if (obj[key] !== null) {
      url += (`${key}=${encodeURIComponent(obj[key])}&`);
    }
  });

  return url.substring(0, url.lastIndexOf('&'));
}

/**
 * 'xxx.js?name=aa'  {name: 'aa'}
 * @param {*} url
 */
function getQueryData(url) {
  // ?_wx_tpl=xxxNative
  // url = url.substring(url.indexOf('.js?') + 3);
  let startIndex = url.indexOf('?_wx_tpl');
  if (startIndex === -1) {
    // ?_wx_tpl=xxx
    // web.html?xxxnative.js?xxx
    startIndex = url.indexOf('.js?');
    if (url.indexOf('.js?') > -1) {
      // native
      startIndex += 3;
    } else {
      // web
      startIndex = url.indexOf('.html?') + 5;
    }
  }
  const _url = url.substring(startIndex);

  const result = {};
  if (_url.indexOf('?') !== -1) {
    const str = _url.substr(1);
    const strs = str.split('&');
    for (let i = 0; i < strs.length; i += 1) {
      result[strs[i].split('=')[0]] = decodeURIComponent(strs[i].split('=')[1]);
    }
  }
  return result;
}

/**
 * hash
 * @param {*} url
 */
function getHash(url) {
  /* get hash */
  const hashIndex = url.indexOf('#');
  const hash = hashIndex > 0 ? url.substring(hashIndex, url.length) : '';

  const queryIndex = url.indexOf('?');
  const result = queryIndex > 0 ? hash.substring(0, queryIndex) : hash;

  return result;
}

function gotoH5(url, params, callback) {
  const query = getQueryData(url);

  let allQuery = createQuery(query);
  if (params) {
    allQuery = createQuery(Object.assign({}, query, params));
  }

  const queryIndex = url.indexOf('?');
  const target = queryIndex > 0 ? url.substring(0, queryIndex) : url;

  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    // webnative
    // target = target.replace(/(.js)$/, '.html');
    window.location.href = `${target}${allQuery}`;
  } else {
    // native
    // webnative
    // target = target.replace(/(.html)$/, '.js');
    navigator.push({
      // route.url
      url: `${target}${allQuery}`,
      animated: 'true',
    }, (event) => {
      if (callback) {
        callback(event);
      }
      // modal.alert({
      //   message: `${target}${allQuery}`
      // });
    });
  }
}

function _push(to, params, isNavi, callback) {
  const matches = /\/\/([^/]+?)\//.exec(to);
  if (matches && matches.length >= 2) {
    // gotoH5
    gotoH5(to, params, callback);
    return;
  }

  const baseUrl = getBaseUrl(isNavi);
  const query = getQueryData(weex.config.bundleUrl);

  let allQuery = createQuery(query);
  if (params) {
    allQuery = createQuery(Object.assign({}, query, params));
  }

  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    window.location.href = `${baseUrl}${to}.html${allQuery}`;
  } else {
    // native
    // bundlejstry-catchnative
    navigator.push({
      // route.url
      url: `${baseUrl}${to}.js${allQuery}`,
      animated: 'true',
    }, (event) => {
      if (callback) {
        callback(event);
      }
    });
  }
}

/**
 * bundlehttp
 * @param {String} to bundlehttp
 * @param {Object} params queryString
 * @param {Function} callback 
 */
function push(to, params, callback) {
  _push(to, params, true, callback);
}

function gotoTab(to, params, callback) {
  _push(to, params, false, callback);
}

function goto(to, params, callback) {
  const baseUrl = `http${config.ENABLE_HTTPS ? 's' : ''}://${config.DOMAIN}`;

  let allQuery = '';
  if (params) {
    allQuery = createQuery(params);
  }

  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    window.location.href = `${baseUrl}/${to}.html${allQuery}`;
  } else {
    // native
    // bundlejstry-catchnative
    navigator.push({
      // route.url
      url: `${baseUrl}${config.DIST}/${to}.js${allQuery}`,
      animated: 'true',
    }, (event) => {
      if (callback) {
        callback(event);
      }
    });
  }
}

function back() {
  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    // window.history.go(-1);
    window.history.back();
  } else {
    // native
    navigator.pop();
  }
}

function getParams() {
  return getQueryData(weex.config.bundleUrl);
}

function close(params, success, failure) {
  const NOOP = () => {};
  navigator.close(params || {}, success || NOOP, failure || NOOP);
}

function replace(to, params, callback) {
  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    const baseUrl = `http${config.ENABLE_HTTPS ? 's' : ''}://${config.DOMAIN}`;

    let allQuery = '';
    if (params) {
      allQuery = createQuery(params);
    }
    window.location.replace(`${baseUrl}/${to}.html${allQuery}`);
  } else {
    // native
    const baseUrl = getBaseUrl(true);
    const query = getQueryData(weex.config.bundleUrl);
    let allQuery = createQuery(query);
    if (params) {
      allQuery = createQuery(Object.assign({}, query, params));
    }
    // // replacepop
    // // androidiosindex.vue
    // navigator.push({
    //   // route.url
    //   url: `${baseUrl}${to}.js${allQuery}`,
    //   animated: 'true',
    // }, (event) => {
    //   if (callback) {
    //     callback(event);
    //   }
    // });
    // navigator.pop({
    //   animated: 'false',
    // });

    // closeandroidActivityios
    // androidindexiosindex
    // iosclose
    // 
    // https://stackoverflow.com/questions/7895274/iphone-remove-view-and-navigationcontroller
    navigator.push({
      // route.url
      url: `${baseUrl}${to}.js${allQuery}`,
      animated: 'true',
    }, (event) => {
      if (WXEnvironment.platform.toLowerCase() === 'ios' && navEvent) {
        // 
        navEvent.removeBefore();
      } else {
        // 
        close();
      }
      if (callback) {
        callback(event);
      }
    });
    // removeLastpushindexremoveLastapp
    // if (WXEnvironment.platform.toLowerCase() === 'ios' && navEvent) {
    //   // ios
    //   navEvent.removeLast(() => {
    //     navigator.push({
    //       // route.url
    //       url: `${baseUrl}${to}.js${allQuery}`,
    //       animated: 'true',
    //     }, (event) => {
    //       if (callback) {
    //         callback(event);
    //       }
    //     });
    //   });
    // } else {
    //   // android
    //   navigator.push({
    //     // route.url
    //     url: `${baseUrl}${to}.js${allQuery}`,
    //     animated: 'true',
    //   }, (event) => {
    //     // 
    //     close();
    //     if (callback) {
    //       callback(event);
    //     }
    //   });
    // }
  }
}

function replaceOnline(to, params, callback) {
  const baseUrl = `http${config.ENABLE_HTTPS ? 's' : ''}://${config.DOMAIN}`;

  let allQuery = '';
  if (params) {
    allQuery = createQuery(params);
  }

  if (WXEnvironment.platform === 'Web' || typeof window === 'object') {
    // web
    window.location.replace(`${baseUrl}/${to}.html${allQuery}`);
  } else {
    // native
    const query = getQueryData(weex.config.bundleUrl);
    allQuery = createQuery(query);
    if (params) {
      allQuery = createQuery(Object.assign({}, query, params));
    }
    navigator.push({
      // route.url
      url: `${baseUrl}${config.DIST}/${to}.js${allQuery}`,
      animated: 'true',
    }, (event) => {
      if (WXEnvironment.platform.toLowerCase() === 'ios' && navEvent) {
        // 
        navEvent.removeBefore();
      } else {
        // 
        close();
      }
      if (callback) {
        callback(event);
      }
    });
  }
}

export default {
  createQuery,
  getQueryData,
  getHash,
  push,
  goto,
  gotoTab,
  gotoH5,
  back,
  getParams,
  replace,
  replaceOnline,
  close,
};



