<template>
  <div class="wrapper">
    <nav-bar title=""></nav-bar>

    <text class="greeting">Hello Weex!</text>
    <text class="message"></text>
    <text class="message" @click="jump('env')"></text>
    <!-- <text class="message" @click="jump('views/test/hello')"></text> -->
    <!-- <text class="message" @click="jumpWithParams('views/test/hello')"></text> -->
    <text class="message" @click="jump('views/test/index')"></text>
    <!-- ! text> -->
    <!-- <text class="message"
        @click="jumpGlobalEvent('views/test/hello', { a: 'hello', b: 'globalEvent' })"
      >GlobalEvent</text> -->
  </div>
</template>

<script>
import NavBar from '@/components/nav-bar';

import globalEvent from '@/utils/globalEvent';

import helper from '../../utils/helper';
import { DOMAIN, DIST, ENABLE_HTTPS } from '../../config';

const modal = weex.requireModule('modal');

export default {
  components: {
    NavBar,
  },
  data() {
    return {};
  },
  methods: {
    jump(to) {
      // views/test/hello
      if (WXEnvironment.platform === 'Web') {
        helper.gotoH5(`http${ENABLE_HTTPS ? 's' : ''}://${DOMAIN}/${to}.html`);
      } else {
        helper.gotoH5(`http${ENABLE_HTTPS ? 's' : ''}://${DOMAIN}${DIST}/${to}.js`);
      }
    },
    jumpWithParams(to) {
      const params = {
        a: 'a1',
        b: 'b1',
      };
      // bundleviews/test/hellodomain/dist/views/test/hello
      // pushbundle
      // helper.push(to, params);
      // helper.push(`http${ENABLE_HTTPS ? 's' : ''}://${DOMAIN}${DIST}/${to}.js`, params);
      // goto+bundle
      helper.goto(to, params);
    },
    jumpGlobalEvent(to, params) {
      // GlobalEvent
      // 
      globalEvent.addEventListener('hello-return', (result) => {
        console.log(`addEventListener callback (hello-return), result: ${result}`);
        modal.toast({
          message: `addEventListener callback (hello-return), result: ${JSON.stringify(result)}`,
        });
        // callback
        globalEvent.removeEventListener('hello-return');
      });
      // 
      // urlqueryStringlocalStorage
      // localStroge
      // globalEvent.fireGlobalEvent('hello-params', params, () => {
      //   console.log('fireGlobalEvent callback (hello-params)');
      //   modal.toast({
      //     message: 'fireGlobalEvent callback (hello-params)',
      //   });
      //   helper.goto(to);
      // });
      helper.goto(to, params);
    },
  },
  beforeDestroy() {
    globalEvent.removeEventListener('hello-return');
  },
};
</script>

<style scoped>
.wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  align-content: center;
}
.greeting {
  margin-top: 70px;
  font-size: 50px;
  color: #41b883;
}
.message {
  margin-top: 30px;
  font-size: 32px;
  color: #727272;
}
</style>



