<template>
  <div class="wrapper">
    <nav-bar title=""></nav-bar>
    <div class="page-wrapper">
      <text class="greeting">Hello Weex!</text>
      <text class="message"></text>
      <text class="message" @click="logout()"></text>
    </div>
  </div>
</template>

<script>
import NavBar from '@/components/nav-bar';

import helper from '@/utils/helper';
import {
  USER_KEY,
} from '@/config';

const storage = weex.requireModule('storage');

export default {
  components: {
    NavBar,
  },
  data() {
    return {
    };
  },
  methods: {
    logout() {
      storage.removeItem(USER_KEY, (e) => {
        if (e.result === 'success') {
          // 
          helper.replace('views/login/login');
        }
      });
    },
  },
};
</script>

<style scoped>
.wrapper {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  /* justify-content:center;web
   weexflex-directioncolumn
   flexboxmain axiscross axis
   flex-direction
   
   */
  /* justify-content: center; */
  align-items: center;
  align-content: center;
}
.page-wrapper {
  flex: 1;
  /* androidiosweb */
  align-items: center;
}
.greeting {
  margin-top: 70px;
  font-size: 50px;
  color: #41B883;
  text-align: center;
}
.message {
  margin-top: 30px;
  font-size: 32px;
  color: #727272;
  text-align: center;
}
</style>



